.reactify--disabled {
  cursor: not-allowed;
  pointer-events: none;
  opacity: 0.6 !important; }
  .reactify--disabled:hover {
    pointer-events: none !important; }

.reactify--hidden {
  display: none !important; }

.reactify--pl-1 {
  padding-left: 1em; }

.reactify--pr-1 {
  padding-right: 1em; }

.reactify--z-index-1 {
