.reactify-stacked-card__container {
  display: inline-flex;
  border-radius: 0.1em;
  margin-left: 7em; }

.reactify-stacked-card--shadowed {
  box-shadow: 0px 0px 5px 0px #888888; }

.reactify-stacked-card__header {
  margin-left: -7em;
  height: 75%;
  align-self: center; }

.reactify-stacked-card__body {
  margin: 1em; }

.reactify-stacked-card__footer {
  margin: 1em; }

.reactify-stacked-card--size-small {
  height: 10em; }

.reactify-stacked-card--size-medium {
  height: 15em; }

.reactify-stacked-card--size-large {
  height: 20em; }

.reactify-stacked-card--bordered {
  border-left: 1px solid darkgray;
  padding-left: 0.8em; }
