.reactify--disabled {
  cursor: not-allowed;
  pointer-events: none;
  opacity: 0.6 !important; }
  .reactify--disabled:hover {
    pointer-events: none !important; }

.reactify--hidden {
  display: none !important; }

.reactify--pl-1 {
  padding-left: 1em; }

.reactify--pr-1 {
  padding-right: 1em; }

.reactify--z-index-1 {
  z-index: 1; }

.reactify--z-index-10 {
  z-index: 10; }

.reactify--z-index-11 {
  z-index: 11; }

.reactify--z-index-100 {
  z-index: 100; }

.reactify--z-index-101 {
  z-index: 101; }

.reactify--z-index-1000 {
  z-index: 1000; }

.reactify--z-index-1001 {
  z-index: 1001; }

.reactify--z-index-10000 {
  z-index: 10000; }

.reactify--z-index-10001 {
  z-index: 10001; }

.reactify-alert__container {
  padding: 0.4em 0.4em;
  border: 1px solid; }

.reactify-alert-border-left {
  border-left: 0.4em solid; }

.reactify-alert-border-top {
  border-top: 0.4em solid; }

.reactify-alert-border-right {
  border-right: 0.4em solid; }

.reactify-alert-border-bottom {
  border-bottom: 0.4em solid; }

.reactify-alert-border--theme-custom {
  border-color: currentColor; }

.reactify-alert-border--theme-default {
  border-color: #141414; }

.reactify-alert-border--theme-dark {
  border-color: #141414; }

.reactify-alert-border--theme-light {
  border-color: #bfbfbf; }

.reactify-alert-border--theme-info {
  border-color: #3498db; }

.reactify-alert-border--theme-warning {
  border-color: #f39c12; }

.reactify-alert-border--theme-danger {
  border-color: #e74c3c; }

.reactify-alert-border--theme-success {
  border-color: #2ecc71; }
