  align-content: center; }

.reactify-user_profile__info {
  padding-left: 0.2em;
  padding-right: 0.2em; }

.reactify-user_profile__img-container {
  width: 2em;
  height: 2em;
  border-radius: 2.4em;
  box-shadow: 0 0 1px #fff;
  overflow: hidden; }

.reactify-user_profile__img {
  width: 100%;
  height: 100%; }

.reactify-user_profile__tray--placement-top {
  position: absolute;
  right: 0px;
  bottom: 1.9em; }

.reactify-user_profile__tray--placement-bottom {
  position: absolute;
  right: 0px;
  top: 1.9em; }

