.reactify--disabled {
  cursor: not-allowed;
  pointer-events: none;
  opacity: 0.6 !important; }
  .reactify--disabled:hover {
    pointer-events: none !important; }

.reactify--hidden {
  display: none !important; }

.reactify--pl-1 {
  padding-left: 1em; }

.reactify--pr-1 {
  padding-right: 1em; }

.reactify--z-index-1 {
  z-index: 1; }

.reactify--z-index-10 {
  z-index: 10; }

.reactify--z-index-11 {
  z-index: 11; }

.reactify--z-index-100 {
  z-index: 100; }

.reactify--z-index-101 {
  z-index: 101; }

.reactify--z-index-1000 {
  z-index: 1000; }

.reactify--z-index-1001 {
