.reactify--disabled {
  cursor: not-allowed;
  pointer-events: none;
  opacity: 0.6 !important; }
  .reactify--disabled:hover {
    pointer-events: none !important; }

.reactify--hidden {
  display: none !important; }

.reactify--pl-1 {
  padding-left: 1em; }

.reactify--pr-1 {
  padding-right: 1em; }

.reactify--z-index-1 {
  z-index: 1; }

.reactify--z-index-10 {
  z-index: 10; }

.reactify--z-index-11 {
  z-index: 11; }

.reactify--z-index-100 {
  z-index: 100; }

.reactify--z-index-101 {
  z-index: 101; }

.reactify--z-index-1000 {
  z-index: 1000; }

.reactify--z-index-1001 {
  z-index: 1001; }

.reactify--z-index-10000 {
  z-index: 10000; }

.reactify--z-index-10001 {
  z-index: 10001; }

.reactify-fullScreen__container {
  position: relative;
  background: white;
  border: 0;
  padding: 0; }

.reactify-fullScreen__icon {
  position: absolute;
  display: inline;
  color: #141414;
  top: 0px;
  right: 0px; }

.reactify-fullScreen--allSpace, .reactify-fullScreen--expanded, .reactify-fullScreen--relative {
  left: 0 !important;
  top: 0 !important;
  right: 0 !important;
  bottom: 0 !important;
  z-index: 1000 !important;
  width: 100% !important;
  height: 100% !important; }

.reactify-fullScreen--expanded {
  position: fixed; }

.reactify-fullScreen--relative {
  position: relative; }
