.reactify--disabled {
  cursor: not-allowed;
  pointer-events: none;
  opacity: 0.6 !important; }
  .reactify--disabled:hover {
    pointer-events: none !important; }

.reactify--hidden {
  display: none !important; }

.reactify--pl-1 {
  padding-left: 1em; }

.reactify--pr-1 {
  padding-right: 1em; }

.reactify--z-index-1 {
  z-index: 1; }

.reactify--z-index-10 {
  z-index: 10; }

.reactify--z-index-11 {
  z-index: 11; }

.reactify--z-index-100 {
  z-index: 100; }

.reactify--z-index-101 {
  z-index: 101; }

.reactify--z-index-1000 {
  z-index: 1000; }

.reactify--z-index-1001 {
  z-index: 1001; }

.reactify--z-index-10000 {
  z-index: 10000; }

.reactify--z-index-10001 {
  z-index: 10001; }

.reactify-legend__container--vertical {
  display: inline-flex;
  font-size: 0.7em;
  flex-direction: column; }

.reactify-legend__container--horizontal {
  display: inline-flex;
  font-size: 0.7em;
  flex-direction: row; }
  .reactify-legend__container--horizontal div:last-child {
    margin-right: 0; }

.reactify-legend-item__container {
  display: inline-flex;
  align-items: center;
  align-content: center;
  margin-right: 0.3em; }

.reactify-legend-item__icon {
  display: inline-block;
  width: 0.7em;
  height: 0.7em;
  border: 1px solid #000;
  opacity: .8;
  margin-right: 0.3em; }
  .reactify-legend-item__icon--theme-default {
    background-color: #141414; }
  .reactify-legend-item__icon--theme-dark {
    background-color: #141414; }
  .reactify-legend-item__icon--theme-light {
    background-color: #bfbfbf; }
  .reactify-legend-item__icon--theme-info {
    background-color: #3498db; }
  .reactify-legend-item__icon--theme-warning {
    background-color: #f39c12; }
  .reactify-legend-item__icon--theme-danger {
    background-color: #e74c3c; }
  .reactify-legend-item__icon--theme-success {
    background-color: #2ecc71; }
