.reactify--disabled {
  cursor: not-allowed;
  pointer-events: none;
  opacity: 0.6 !important; }
  .reactify--disabled:hover {
    pointer-events: none !important; }

.reactify--hidden {
  display: none !important; }

.reactify--pl-1 {
  padding-left: 1em; }

.reactify--pr-1 {
  padding-right: 1em; }

.reactify--z-index-1 {
  z-index: 1; }

.reactify--z-index-10 {
  z-index: 10; }

.reactify--z-index-11 {
  z-index: 11; }

.reactify--z-index-100 {
  z-index: 100; }

.reactify--z-index-101 {
  z-index: 101; }

.reactify--z-index-1000 {
  z-index: 1000; }

.reactify--z-index-1001 {
  z-index: 1001; }

.reactify--z-index-10000 {
  z-index: 10000; }

.reactify--z-index-10001 {
  z-index: 10001; }

.reactify-snackbar__container {
  position: fixed;
  z-index: 100000;
  opacity: 0.9;
  display: inline;
  border-radius: 3px; }

.reactify-snackbar__options {
  display: inline-flex;
  direction: row;
  justify-content: space-between;
  padding: 0.4em 0.8em; }

.reactify-snackbar__action {
  cursor: pointer; }

.reactify-snackbar__dismiss {
  cursor: pointer; }

.reactify-snackbar--position-top-left {
  top: 10px;
  left: 10px; }

.reactify-snackbar--position-top-right {
  top: 10px;
  right: 10px; }

.reactify-snackbar--position-bottom-left {
  bottom: 10px;
  left: 10px; }

.reactify-snackbar--position-bottom-right {
  bottom: 10px;
  right: 10px; }

.reactify-snackbar--theme-default {
  background-color: #141414;
  color: white; }

.reactify-snackbar--theme-dark {
  background-color: #141414;
  color: white; }

.reactify-snackbar--theme-light {
  background-color: #d9d9d9;
  color: black; }

.reactify-snackbar--theme-info {
  background-color: #3498db;
  color: white; }

.reactify-snackbar--theme-warning {
  background-color: #f39c12;
  color: white; }

.reactify-snackbar--theme-danger {
  background-color: #e74c3c;
  color: white; }

.reactify-snackbar--theme-success {
  background-color: #2ecc71;
  color: white; }

.reactify-snackbar--theme-transparent {
  background: transparent;
  opacity: 1; }
